    width: 224px;
    cursor: pointer;
    position: relative;
    &__active {
        transition: opacity 0.4s ease-in-out;
        position: absolute; 
        left: 50%; 
        top: 50%; 
        transform: translate(-50%, -50%);
    }
    &__play_btn, &__pause_btn {
        position: absolute;
        left: 50%;
        top: 50%;
        transform: translate(-50%, -50%);
        transition: opacity 0.4s ease-in-out;
    }
    img {
        max-width: 100%;
    }
    &__audio {
        visibility: hidden;
    }
    &_wrapper {
        width: calc(50% - 7.2%);
        height: 250px;
        margin-bottom: 14.7%;
        display: flex;
        align-items: center;
        justify-content: flex-end;
        &:first-of-type, &:nth-of-type(2) {
            margin-top: 14.7%;
        } 
        &:nth-of-type(2n) {
            justify-content: flex-start;
        }
    }
    @media(max-width: 768px) {
        width: 173px;
        &_wrapper {
            width: 100%;
            height: 174px;
            margin-bottom: 119px;
            justify-content: center;
            &:first-of-type {
                margin-top: 135px;
            }
            &:last-of-type {
                margin-bottom: 147px;
            }

            &:nth-of-type(2n) {
                justify-content: center;
                margin-top: 0;
            }
        }
        &__play_btn {
            width: 22px;
        } 
        &__pause_btn {
            width: 24px;
        }
    }
}
</style>
